/* Reservation.css */
.reservation-container {
    max-width: 680px;
    max-height: 930px;
    margin: 50px auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    /*box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);*/
}

.select-container {
    margin-bottom: 20px;
    width: 400px;

}

select {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: box-shadow 0.3s ease;
}

label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.reservation-button {
    width: 140px;
    height: 40px;
    font-size: 14px;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: salmon;
    color: #fff;
    transition: background-color 0.3s ease;
}

.reservation-button:hover {
    background-color: #d0685d;
}

.seat-selection-container {
    margin-bottom: 20px;
}

.seat {
    width: 40px;
    height: 40px;
    margin: 5px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #cccccc;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease;
}

.seat:hover {
    background-color: #868686;
}
.reservation-container h1{
    color: #000;
}
.reserved {
    background-color: #e83a25;
    color: #fff;
    cursor: not-allowed;
}

.seat.selected {
    background-color: #484d52;
}

.seat.selected:hover {
    background-color: #484d52;
}

.reserved:hover {
    background-color: #e74c3c;
}

.seat-grid {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    gap: 10px;
}

.seat-row {
    display: flex;
}

.reservation-summary {
    text-align: center;
    margin-bottom: 10px;
}

p {
    margin-bottom: 10px;
}

.reservation-img img{
    width: 180px;
    margin-right: 10px;
}
.reservation-container-choice{
    display: flex;
    width: 680px;
    justify-content: space-between;
    padding-bottom: 30px;
    border-bottom: 1px solid #cbcbcb;
    margin-bottom: 20px;
}