/* login.css */

.login-container {
    width: 900px;
    padding: 20px 20px;
    padding-top: 80px;
    height: 550px;
    margin: 10px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    background-color: #fff;
}
.login-title{
    font-size: 40px;
    font-weight: 600;
    color: black;
    margin-bottom: 80px ;
}

.login-input {
    width: 730px;
    height: 50px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.loginbtn {
    width: 735.335px;
    height: 53.335px;
    padding: 8px;
    background-color: salmon;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-bottom: 30px;
}

button:hover {
    background-color: darksalmon;
}
.linkToSignup{
    font-style: italic;
    text-decoration: underline;
    color: black;
    opacity: 0.5;

}