.cart-header-content{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #888888;
}
.cart-header-letter{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.cart-header{
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 40px 0;
    margin-top: 80px !important;
}
.clicked{
    color: red !important;
}
.cart-icon img{
    width: 40px;
}
.cart-icon{
    margin-right: 20px;
}
.step{
    font-size: 14px;
    font-weight: 300;
}
.letter{
    font-size: 25px;
}
.cart-main{
    width: 980px;
    margin: auto;
}
.cart-header{
    padding: 0 88px;
}
.chc{
    width: 34px !important;
}
.right-arrow{
    width: 15px;
}
.cart-info{
    margin-top: 80px;
    border-bottom: 1px solid black;
    margin-bottom: 20px;
}
.cart-info-1 a{
    width: 20px;
    height: 20px;
    border: 1px solid black;
}
.cart-info-header{
    border-bottom: 3px solid black;
    color: black;
    padding: 5px 0;
    font-size: 19px;
}
.cart-info-1{
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 3px solid #000000;
    background-color: #fafafa;
}
.cart-info-1 div, .cart-info-2>div{
    padding: 16px 10px;
    font-size: 16px;
    color: black;

    display: flex;
    align-items: center;
    justify-content: center;

}
input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-right: 30px;
}
.cart-info-2{
    background-color: #fff !important;
    display: flex;
    border-top: 1px solid #d6d6d6;
}
.cart-info-2>div{
    background-color: #fff !important;

}
.cart-info-2>div:nth-child(2){
    justify-content: flex-start;
}
.cart-info-1 div:nth-child(2){
    width: 40%;
}
.cart-info-1 div:nth-child(3){
    width: 20%;
}
.cart-info-1 div:nth-child(4){
    width: 20%;
}
.cart-info-1 div:nth-child(5){
    width: 20%;
}
.ci1{
    width: 40%;
}
.ci2{
    width: 20%;
}
.ci3{
    width: 20%;
}
.ci4{
    width: 20%;
}
.cart-warning{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
}
.delete-cart{
    border: 1px solid #cecece;
    padding: 10px 12px;
    font-size: 14px;
    cursor: pointer;
}
.product-name{
    font-weight: 500;
    font-size: 16px;
    color: black;
    margin-bottom: 8px;
}
.product-detail{
    font-weight: 400;
    font-size: 15px;
    color: #6a6f77;
}
.ci-img{
    width: 90px;
    height: 90px;
    margin-right: 20px;
}
.ci-img img{
    width: 100%;
    height: 100%;
}
.fp-header{
    width: 100%;
    background-color: #fafafa;
    color: black;
    font-size: 16px;
    display: flex;
    justify-content: space-around;
    padding: 20px 0;
    border-top:1px solid #d6d6d6;
}
.fp-main{
    font-size: 26px;
    width: 100%;
    color: black;
    display: flex;
    justify-content: space-around;
    padding: 30px 0;
    margin-bottom: 30px;
}
.final{
    color: #fb4357;
}
.payBtn{
    margin-bottom: 30px;
}
.final-pay{
    display: flex;
    width: 980px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.discount{
    margin: 0 30px ;
    padding-right: 14px;
}
.final{
    margin-right: 13px;
}