.reservation-history-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.reservation-list {
    margin-top: 20px;
}

.reservation-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    position: relative;
}

.cancel-button {
    position: absolute;
    top: 5px;
    right: 5px;
    color: salmon;
    background-color: #fff;
    border: none;
    width: 30px;
    height: 30px;
    font-size: 20px;
    cursor: pointer;
    border-radius: 50%;
    transition: ease-in-out 0.2s;
}

.cancel-button:hover {
    color: white;
    background-color: salmon;
}