.Swiper-Container{
    height: 600px;
}

.movie-rank{
    height: 462px;
    width: 980px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.rank-container{
    width: 100%;
    background-color: #FBFBFB;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Home-container{
    width: 980px;
    margin: 30px auto;
}
.store-container{
    width: 980px;
    margin: 40px auto;
    display: flex;
    justify-content: space-between;
}
.store-product-title{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.spt1{
    font-weight: 600;
    font-size: 17px;
    color: #222
}
.spt2{
    padding: 2px 15px 3px;
    font-weight: 400;
    font-size: 12px;
    color: #666;
    line-height: 1.417em;
    border: 1px solid #f4f4f4;
    border-radius: 11px;
}
.store-product-content{
    display: flex;
    align-items: center;
}
.spc1{
    width: 75px;
    height: 76px;
}
.spc1>img{
    width: 100%;
    height: 100%;
}
.spc2{
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    color: black;
    font-weight: 600;
    font-size: 14px;
}
.spc2 div{
    padding: 5px 0px;
}
.store-product-content{
    margin-top: 27px;
}
.movie-rank-title {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.cinema-title{
    font-weight: 700;
    color: #222;
    font-size: 26px;
    margin-bottom: 30px;
}
.separator {
    position: relative;
    background-color: gainsboro;
    width: 1px;
    height: 15px;
    margin: 0px 10px;
}

.mrt{
    font-weight: 400;
    font-size: 26px;
    color: #666;
}
.mrt-clicked{
    font-weight: 700;
    color: #222;
}
.store-product{
    width: 270px;
    padding: 20px 19px 30px;
    border: 1px solid #e4e4e4;
    border-radius: 10px;
}