.mypage-main{
    width: 980px;
    margin: 40px auto;
    text-align: center;
}
.mypage-header{
    height: 160px;
    background-color: #fafafa;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.mypage-header h1{
    margin-top: 0px;
    margin-bottom: 0px;
    color: #000;
    font-size: 30px;
}
.mypage-header p{
    margin-top: 0px;
    color: #777777;
    font-size: 30px;
}
.circle-links {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}

.circle-links li {
    margin: 0 40px;
}

.circle-link {
    width: 200px; /* 원의 지름 설정 */
    height: 200px; /* 원의 지름 설정 */
    background-color: #fff; /* 하얀 배경색 설정 */
    color: #333; /* 글자색 설정 */
    border-radius: 50%; /* 원형 모양을 만들기 위한 속성 */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    border: 1px solid #ccc; /* 테두리 추가 */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 그림자 추가 */
}
.circle-link:hover {
    background-color: #f2f2f2; /* hover 시 배경색 변경 */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* hover 시 그림자 효과 강화 */
    transform: scale(1.1); /* hover 시 확대 효과 */
    transition: background-color 0.3s, box-shadow 0.3s, transform 0.3s; /* transition 추가 */
}
.icon {
    /* 내 아이콘 스타일 */
}

.text {
    margin-top: 10px;
    font-size: 12px;
    line-height: 1.4;
}
.mypage{
    height: 670px;
}