.store-main {
    width: 980px;
    margin: 40px auto;
}

.store-title {
    font-weight: 600;
    font-size: 35px;
    color: black;
    border-bottom: 2px solid black;
    padding-bottom: 15px;
}

li {
    list-style-type: none;
}

.store-nav {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 20px 0;
    border-bottom: 1px solid gainsboro;
    margin-bottom: 40px;
}

.store-nav-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.store-nav div {
    font-size: 18px;
    color: black;
    margin-right: 50px;
    cursor: pointer;
}

.cart {
    margin: 0 !important;
}

.cartcnt {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    line-height: 20px;
    width: 20px;
    height: 20px;
    font-weight: 500;
    font-size: 13px;
    color: #fff;
    background-color: #fb4357;
    border-radius: 50%;
    outline: none;
    margin-left: 8px;
}

.cart {
    display: flex;
    align-items: center;
}

.active {
    color: salmon !important;
    font-weight: 600;
}

.inactive {
    color: gray !important;
}

.card-main {
    margin-bottom: 130px;
}

.card-title {
    color: black;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #777777;
    margin-bottom: 40px;
}

.card-title h1 {
    font-size: 30px;
}

.card-title div {
    font-size: 20px;
    margin-left: 20px;
    font-weight: 300;
}

a {
    text-decoration: none;
    color: black;
}

.card-content {
    display: flex;
    margin-left: 20px;
    flex-wrap: wrap;
    justify-content: flex-start;
}

.card-content:hover {
    cursor: pointer;
}

.card-img {
    position: relative;
    overflow: hidden;
}

.card-img img {
    width: 270px;
    transition: transform 0.3s ease;
}

.card-img img:hover {
    transform: scale(1.1);
}

.card {
    width: 30%;
    margin-bottom: 20px;
    box-sizing: border-box;
    margin-right: 32px;
}

.card-name {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 6px;
}

.card-detail {
    font-size: 16px;
    margin-bottom: 10px;
}

.card-price {
    font-size: 16px;
}

/*spinner*/
/* Snack.css */
.spinner {
    border: 8px solid rgba(0, 0, 0, 0.1);
    border-top: 8px solid salmon;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    margin: 20px auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.detail-header {
    display: flex;
    justify-content: space-between;
    font-size: 26px;
    font-weight: 600;
    color: #000;
    padding-bottom: 10px;
    border-bottom: 1.5px solid #000;
}

.detail-header img {
    width: 24px;
    cursor: pointer;
    margin-right: 12px;
}

.detail-content {
    display: flex;
    padding: 36px 0;
    border-bottom: 1.5px solid #000;
}

.detail-img {
    width: 420px;
    height: 420px;
}

.detail-img img {
    width: 100%;
    height: 100%;
}

.detail-text {
    width: 500px;
    height: auto;
    padding: 20px 30px;
}

.detail-price {
    color: #000;
    border-bottom: 1px solid #d6d6d6;
    padding: 10px 0;
    width: 100%;
    font-size: 24px;
}

.detail-info-box {
    display: flex;
    padding: 16px 0;
    border-bottom: 1px solid #d6d6d6;
}
.dt-info-title{
    margin-right: 20px;
}
dt, dd {
    font-size: 14px;
    color: #000;
    margin: 6px 0;
}

dd {
    font-weight: 300;
}

.detail-info-content dd:last-child {
    text-decoration: #d2d2d2 underline;
}

.quantityAndPrice {
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 2px solid gray;
    padding: 15px 0;
}

.quantity-controls {
    border: 1px solid #bdc1c8;
    width: fit-content;
    font-size: 20px;
    display: flex;
    align-items: center;
    color: #000;
}

.quantity-controls span {
    padding: 0 10px;
    padding-bottom: 3px;
}

button:hover {
    cursor: pointer;
}

.quantity-controls button {
    background-color: #fff;
    border: none;
    width: 28px;
    height: 28px;
    font-size: 20px;
    font-weight: 200;
    display: flex;
    align-items: center;
    justify-content: center;
}
.minusBtn{
    padding-bottom: 4px;
}
.plusBtn{
    padding-top: 3px;
}
.qap-price {
    color: #000;
    font-size: 20px;
}

.totalPrice {
    color: #000;
    font-size: 18px;
    display: flex;
    align-items: center;
    padding: 20px 0;
    justify-content: flex-end;
}

.totalPrice span {
    color: #fb4357;
    font-size: 21px;
    padding-left: 18px;
}
.cartBtn{
    background-color:#fb4357 !important;
    width: 240px;
    height: 60px;
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    border: none;
}
.btn-box{
    display: flex;
    justify-content: space-between;
}
.btn-box a{
    display: flex;
    align-items: center;
    justify-content: center;
}
.buyBtn{
    width: 240px;
    height: 60px;
    background-color: #373e46;
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    border: none;
}
.buyBtn:hover{
    background-color: #67717c !important;
}
.cartBtn:hover{
    background-color: #fd9299 !important;
}
.ddTitle{
    font-weight: 500;
    font-size: 18px;
    line-height: 26px;
    letter-spacing: 0.014em;
    margin-top: 40px;
}
.ddText{
    font-weight: 300;
    font-size: 15px;
    line-height: 22px;
    letter-spacing: 0.017em;
}
dl{
    margin-top: 30px;
}