.movie-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 980px;
    margin: 40px auto;
}
.Movies-title{
    width: 980px;
    margin: 40px auto;
    font-weight: 600;
    font-size: 30px;
    color: black;
    border-bottom: 2px solid gainsboro;
    padding-bottom: 20px;
}
.movie-card {
    width: 200px;
    margin-bottom: 40px;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 그림자 스타일 조절 가능 */
    transition: transform 0.3s ease-in-out;

    &:hover {
        transform: scale(1.05); /* 마우스 호버 시 크기 조절 */
        cursor: pointer;
    }
}
.booking-btn{
    width: 97px;
    height: 25px;
    color: white;
    background-color: salmon;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.movie-title{
    font-size: 16px;
    font-weight: 600;
    color: black;
    margin-top: 8px;
}
.movie-chart-header{
    width: 100%;
    background-color: #505050;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
}
.movie-chart-item img{
    width: 100% !important;
    height: 280px !important;
}
.movie-chart-item{
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 200px;
}
.movie-info{
    margin-bottom: 8px;
}
strong{
    color: black;
}
