.purchase-history-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.order-list {
    margin-top: 20px;
}

.order-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.order-details {
    display: flex;
    align-items: center;
    height: 100px;
}

.order-details img {
    width: 100px;
    height: 100px;
    margin-right: 20px;
}

.order-text {
    width: 420px;
    font-size: 16px;
    padding-left: 20px;
}
.ot2{
    font-size: 12px;
}

.order-status p {
    margin: 0;
    font-weight: bold;
    color: green; /* 주문 완료 상태에 대한 스타일 지정 */
}
.ot1{
    font-size: 20px;
    color: #000;
    font-weight: 600;
    margin-top: 10px;
}
.totalPriceOrdered{
    font-size: 20px;
    color: #000;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-bottom: 20px;
    margin-right: 40px;
}
.order-warning{
    display: flex;
    justify-content: flex-end;
}
.order-status{
    height: 50px;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    margin-top: 25px;
}
.order-status p{
    font-size: 16px;
}
.ot3{
    display: flex;
}
.ot3-1{
    width: 140px;
}