.imageMenu{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.menu-item{
    width: auto;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 19px 25px 19px 19px;
    border-top: 1px solid #E9E9E9;
    cursor: pointer;
}
.menu-item:hover {
    background-color: #f8f8f7;
    border-radius: 10px;
}
.menu-container{
    display: flex;
    flex-direction: column;
    width: 400px;
}
.im2{
    border-bottom: 1px solid #E9E9E9;;
}
.im1{
    width: 500px;
    height: auto;
}
.item-title{
    font-weight: 600;
    color: black;
    font-size: 18px;
}
.item-hash{
    padding: 2px 7px;
    font-size: 14px;
    color: #666;
    background-color: #f6f6f6;
    border-radius: 5px;
}