/* App.css */

.navbar {
  display: flex;
  align-items: center;
  top: 0;
  bottom: auto;
  z-index: 1;
  border-bottom: 3px solid salmon;
}
.nav-item {
  float: left;
  display: block;
  text-align: center;
  text-decoration: none;
  font-weight: 500;
  font-size: 16px;
  color: #222;
  line-height: 1.500em;
  margin-right: 55px;
}

.navbar-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.nc1{
  width: 100%;
  border-bottom: 1px solid #eee;
}
.nc2{
  padding: 5px;
}
.logo{
  width: 80px;
  height: auto;
}
.header-content{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 980px;
  margin: 4px auto;
}
.nav{
  display: flex;
  align-items: center;
  width: 980px;
  margin: 4px auto;
}
.header-img{
  width: 20px;
}
.header-user{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  justify-content: space-between;
  color: #666;;
}
.header-icons{
  width: 440px;
  display: flex;
  justify-content: right;
}
.header-icons a{
  display: flex;
  flex-direction: column;
  margin: 0px 20px;
  width: 60px;
  cursor: pointer;
}
a>div>img{
  margin-bottom: 4px;
}
.nav-item {
  color: gray;
}
.nav-item.active {
  color: salmon; /* 활성 상태의 원하는 색상으로 변경하세요 */
  font-weight: 600;
}