.theater-header{
    width: 980px;
    margin: 50px auto;
    display: flex;
    align-items: center;
    justify-content: center;
}
.theater-main{
    width: 980px;
    margin: 40px auto;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.theater-theater {
    width: 100%;
    padding-bottom: 40px;
    border-bottom: 2px solid gainsboro;
    padding-top: 12px;
}
.theater-content{
    color: #333333;
    font-size: 29px;
    font-weight: 500;
    vertical-align: top;
}
.theater-box{
    background-color: #212121;
    padding: 20px 30px;
    color: gainsboro;
}
.theater-name{
    margin-bottom: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 600;
}
.tc-btn{
    background-color: salmon;
    color: #fff;
    border: none;
    padding: 10px 14px;
    border-radius: 5px;
    font-weight: 600;
    cursor: pointer;
}